export default class ImmutableOrderedMultiSet<T> {
  private values: [T, number][];
  private comparisonFn: (a: T, b: T) => boolean;

  constructor(
    comparisonFn: (a: T, b: T) => boolean,
    values: [T, number][] = []
  ) {
    this.values = values;
    this.comparisonFn = comparisonFn;
  }

  put(t: T, amount: number = 1): ImmutableOrderedMultiSet<T> {
    let values: [T, number][] = this.mapToArray((value, count) => [
      value,
      count,
    ]);
    let found = false;
    for (let i = 0; i < values.length; i++) {
      if (this.comparisonFn(values[i][0], t)) {
        values[i][1] += amount;
        found = true;
        break;
      }
    }
    if (!found) {
      values.push([t, amount]);
    }
    return new ImmutableOrderedMultiSet(this.comparisonFn, values);
  }

  remove(t: T): ImmutableOrderedMultiSet<T> {
    let values: [T, number][] = this.mapToArray((value, count) => [
      value,
      count,
    ]);
    for (let i = 0; i < values.length; i++) {
      if (this.comparisonFn(values[i][0], t)) {
        values[i][1] -= 1;
        if (values[i][1] === 0) {
          values.splice(i, 1);
        }
        break;
      }
    }
    return new ImmutableOrderedMultiSet(this.comparisonFn, values);
  }

  size(): number {
    return this.mapToArray((value, count) => count).reduce((a, b) => a + b, 0);
  }

  forEach(fn: (value: T, count: number, index: number) => void): void {
    this.mapToArray(fn);
  }

  mapToArray<R>(fn: (value: T, count: number, index: number) => R): R[] {
    return this.values.map((pair, index) => fn(pair[0], pair[1], index));
  }
}
