import { useState } from "react";
import { useParams } from "react-router-dom";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import {
  Alert,
  Card,
  Stack,
  Typography,
  Paper,
  Grid,
  Box,
} from "@mui/material";
import { ButtonUnstyled } from "@mui/base";
import styled from "@emotion/styled";

const PileButton = styled(ButtonUnstyled)(() => ({
  margin: 5,
  cursor: "pointer",
}));

function DraftPage() {
  const [webSocketClient, setWebSocketClient] = useState<W3CWebSocket | null>(
    null
  );
  const [playerView, setPlayerView] = useState<PlayerView | null>(null);
  const [cardImageUrl, setCardImageUrl] = useState<string | null>(null);

  let { draftId, userId } = useParams();

  let baseUrl: string = "http://mtgdraft-server.herokuapp.com";

  if (draftId) {
    if (userId) {
      if (webSocketClient == null) {
        let client = new W3CWebSocket(
          `ws://mtgdraft-server.herokuapp.com/draft/${draftId}/${userId}`
        );
        client.onopen = () => {
          console.log("WebSocket Client Connected");
        };
        client.onmessage = (message) => {
          let content = message.data;
          if (typeof content == "string") {
            setPlayerView(JSON.parse(content));
          } else {
            console.log("Got a non-string msg");
          }
        };
        setWebSocketClient(client);
        return <p>Loading...</p>;
      } else if (playerView == null) {
        return <p>Loading...</p>;
      } else {
        return (
          <Grid
            container
            alignItems="center"
            flexDirection="column"
            justifyContent="space-between"
          >
            {playerView.isDraftFinished ? (
              <Alert severity="success">Draft done</Alert>
            ) : playerView.isPicking ? (
              <Alert severity="info">You are picking</Alert>
            ) : (
              <Alert severity="warning">Waiting for opponent to pick</Alert>
            )}
            <Grid
              container
              item
              xs={11}
              sx={{ maxHeight: "60vh" }}
              justifyContent="space-between"
            >
              <Grid item sm={2} lg={2}>
                <Card
                  sx={{
                    border: "1px solid lightgray",
                    padding: 1,
                    backgroundColor: "grey.100",
                  }}
                  elevation={1}
                >
                  <Typography variant="h5">Opponent's last pick:</Typography>
                  <Stack>
                    {playerView.opponentsLastPick.map((card) => (
                      <Paper
                        sx={{ padding: 1, width: "fit-content", marginY: 1 }}
                        elevation={4}
                        onMouseOver={() => setCardImageUrl(card.imageUrl)}
                      >
                        {card.name}
                      </Paper>
                    ))}
                  </Stack>
                </Card>
              </Grid>
              <Grid item sm={6.8} lg={6.8}>
                <Card
                  sx={{
                    border: "1px solid lightgray",
                    padding: 1,
                    backgroundColor: "grey.100",
                  }}
                  elevation={1}
                >
                  <Typography variant="h5">Piles to pick from:</Typography>
                  <Box>
                    {playerView.pickOptions.map((pickOption, index) => (
                      <PileButton
                        disabled={!playerView.isPicking}
                        onClick={() => {
                          if (playerView.isPicking) {
                            webSocketClient.send(index);
                          }
                        }}
                      >
                        <Stack>
                          <Typography variant="h6">Pile {index + 1}</Typography>
                          {pickOption.map((card) => (
                            <Paper
                              sx={{
                                padding: 1,
                                width: "fit-content",
                                marginY: 1,
                              }}
                              elevation={4}
                              onMouseOver={() => setCardImageUrl(card.imageUrl)}
                            >
                              {card.name}
                            </Paper>
                          ))}
                        </Stack>
                      </PileButton>
                    ))}
                  </Box>
                </Card>
              </Grid>
              <Grid item sm={3} lg={3}>
                <Card
                  sx={{
                    textAlign: "right",
                  }}
                >
                  {cardImageUrl ? (
                    <img height="auto" width="100%" src={cardImageUrl} />
                  ) : (
                    <></>
                  )}
                </Card>
              </Grid>
            </Grid>
            <Grid container item flexDirection="column">
              <Grid>
                <Typography variant="h5">
                  Previous picks ({playerView.previousPicks.length}):
                </Typography>
                {playerView.isDraftFinished ? (
                  <a href={`${baseUrl}/deckbuild/${draftId}/${userId}`}>
                    Go to deckbuilding page
                  </a>
                ) : (
                  <></>
                )}
              </Grid>
              <Grid container item columnGap={1}>
                {playerView.previousPicks.map((card) => (
                  <Grid item sx={{ width: "fit-content" }}>
                    <Paper
                      sx={{ padding: 1, width: "fit-content", marginY: 1 }}
                      elevation={4}
                      onMouseOver={() => setCardImageUrl(card.imageUrl)}
                    >
                      {card.name}
                    </Paper>
                  </Grid>
                ))}
              </Grid>
            </Grid>
          </Grid>
        );
      }
    }
  }

  return <p>Missing url params</p>;
}

interface PlayerView {
  isDraftFinished: boolean;
  isPicking: boolean;
  playerName: string;
  pickOptions: Array<Array<Card>>;
  previousPicks: Array<Card>;
  opponentsLastPick: Array<Card>;
}

interface Card {
  name: string;
  imageUrl: string;
  rarity: string;
}

export default DraftPage;
