import { Button, Grid, IconButton, Stack } from "@mui/material";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import ImmutableOrderedMultiSet from "./ImmutableOrderedMultiSet";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

function initialDeckState(picks: Array<Card>): DeckState {
  let sideboard: ImmutableOrderedMultiSet<Card> =
    new ImmutableOrderedMultiSet<Card>(
      (card1, card2) => card1.name === card2.name
    );

  let basicLands: Array<Card> = [
    {
      name: "Mountain",
      imageUrl:
        "https://c1.scryfall.com/file/scryfall-cards/normal/front/0/3/03bacab3-25fb-4a0c-81b3-7e9e22899c2c.jpg?1562609428",
      rarity: "common",
    },
    {
      name: "Plains",
      imageUrl:
        "https://c1.scryfall.com/file/scryfall-cards/normal/front/1/0/10a087d3-54b1-4000-89d1-0c428659158c.jpg?1616125729",
      rarity: "common",
    },
    {
      name: "Forest",
      imageUrl:
        "https://c1.scryfall.com/file/scryfall-cards/normal/front/1/9/1908f8d2-95df-44e3-8943-568c596cd8aa.jpg?1562610174",
      rarity: "common",
    },
    {
      name: "Island",
      imageUrl:
        "https://c1.scryfall.com/file/scryfall-cards/normal/front/2/0/20b4cbcc-c394-4be3-8072-8893b48c866d.jpg?1562610494",
      rarity: "common",
    },
    {
      name: "Swamp",
      imageUrl:
        "https://c1.scryfall.com/file/scryfall-cards/normal/front/7/c/7cd6becc-f06a-4fd3-8305-70604b92a187.jpg?1562613723",
      rarity: "common",
    },
  ];

  basicLands.forEach((card) => (sideboard = sideboard.put(card, 50)));

  picks.forEach((card) => (sideboard = sideboard.put(card)));

  return {
    mainboard: new ImmutableOrderedMultiSet<Card>(
      (card1, card2) => card1.name === card2.name
    ),
    sideboard,
  };
}

function copyDeckToClipboard(deckState: DeckState) {
  let deckString = "// Mainboard\n";
  deckState.mainboard.forEach(
    (card, count) => (deckString += `${count} ${card.name}\n`)
  );
  deckString += "// Sideboard\n";
  deckState.sideboard.forEach(
    (card, count) => (deckString += `SB: ${count} ${card.name}\n`)
  );

  navigator.clipboard
    .writeText(deckString)
    .then(() => alert("Deck copied to clipboard"));
}

function DeckBuildPage() {
  const [deckState, setDeckState] = useState<DeckState | null>(null);
  const [cardImageUrl, setCardImageUrl] = useState<string | null>(null);

  let { draftId, userId } = useParams();

  useEffect(() => {
    fetch(`http://mtgdraft-server.herokuapp.com/picks/${draftId}/${userId}`, {
      method: "GET",
    })
      .then((response) => response.json())
      .then((data) => setDeckState(initialDeckState(data)));
  }, []);

  return (
    <>
      {deckState == null ? (
        <p>Loading...</p>
      ) : (
        <Stack flexDirection="row">
          <TableContainer component={Paper} sx={{ width: "fit-content" }}>
            <Table size="small" sx={{ width: "fit-content" }} padding="none">
              <TableHead>
                <TableRow>
                  <TableCell>{deckState.mainboard.size()}</TableCell>
                  <TableCell align="right">M</TableCell>
                  <TableCell>ainboard</TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {deckState.mainboard.mapToArray((card, count) => (
                  <TableRow onMouseOver={() => setCardImageUrl(card.imageUrl)}>
                    <TableCell>{count}</TableCell>
                    <TableCell></TableCell>
                    <TableCell>{card.name}</TableCell>
                    <TableCell>
                      <IconButton
                        sx={{ padding: 0 }}
                        size="small"
                        onClick={() => {
                          setDeckState({
                            mainboard: deckState.mainboard.remove(card),
                            sideboard: deckState.sideboard.put(card),
                          });
                        }}
                      >
                        →
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>

          <TableContainer component={Paper} sx={{ width: "fit-content" }}>
            <Table size="small" sx={{ width: "fit-content" }} padding="none">
              <TableHead>
                <TableRow>
                  <TableCell>{deckState.sideboard.size()}</TableCell>
                  <TableCell></TableCell>
                  <TableCell>S</TableCell>
                  <TableCell>ideboard</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {deckState.sideboard.mapToArray((card, count) => (
                  <TableRow onMouseOver={() => setCardImageUrl(card.imageUrl)}>
                    <TableCell>
                      <IconButton
                        sx={{ padding: 0 }}
                        size="small"
                        onClick={() => {
                          setDeckState({
                            mainboard: deckState.mainboard.put(card),
                            sideboard: deckState.sideboard.remove(card),
                          });
                        }}
                      >
                        ←
                      </IconButton>
                    </TableCell>
                    <TableCell>{count}</TableCell>
                    <TableCell> </TableCell>
                    <TableCell>{card.name}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <Stack>
            <Button
              variant="outlined"
              sx={{ margin: 1 }}
              onClick={() => {
                copyDeckToClipboard(deckState);
              }}
            >
              Copy deck to clipboard
            </Button>
            {cardImageUrl ? (
              <img height="auto" width="100%" src={cardImageUrl} />
            ) : (
              <></>
            )}
          </Stack>
        </Stack>
      )}
    </>
  );
}

interface DeckState {
  mainboard: ImmutableOrderedMultiSet<Card>;
  sideboard: ImmutableOrderedMultiSet<Card>;
}

interface Card {
  name: string;
  imageUrl: string;
  rarity: string;
}

export default DeckBuildPage;
