import "./App.css";
import CreateDraftPage from "./CreateDraftPage";
import { Routes, Route } from "react-router-dom";
import DraftPage from "./DraftPage";
import DeckBuildPage from "./DeckBuildPage";

function App() {
  let prefix = "mtgdraft"
  return (
    <Routes>
      <Route path="/" element={<CreateDraftPage />}></Route>
      <Route path="/draft/:draftId/:userId" element={<DraftPage />}></Route>
      <Route
        path="/deckbuild/:draftId/:userId"
        element={<DeckBuildPage />}
      ></Route>
    </Routes>
  );
}

export default App;
