import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";

interface DraftCreated {
  draftId: string;
  playerOne: string;
  playerTwo: String;
}

function CreateDraftPage() {
  const [draftCreated, setDraftCreated] = useState<DraftCreated | null>(null);

  let baseUrl: string = "http://mtgdraft-server.herokuapp.com:3000";

  return (
    <div className="App">
      {draftCreated == null ? (
        <div>
          <button
            onClick={() => {
              fetch("http://mtgdraft-server.herokuapp.com/draft", {
                method: "POST",
              })
                .then((response) => response.json())
                .then((data) => setDraftCreated(data));
            }}
          >
            Create a Winchester draft
          </button>
        </div>
      ) : (
        <>
          <p>Draft created:</p>
          <p>
            Player one head over to{" "}
            <a
              href={`${baseUrl}/draft/${draftCreated.draftId}/${draftCreated.playerOne}`}
            >
              {baseUrl}/draft/{draftCreated.draftId}/{draftCreated.playerOne}
            </a>
          </p>
          <p>
            Player two head over to {baseUrl}/draft/{draftCreated.draftId}/
            {draftCreated.playerTwo}
          </p>
        </>
      )}
    </div>
  );
}

export default CreateDraftPage;
